angular
    .module('tuktukV2Dahboard')
    .factory('MapService', MapService)

function MapService($q, $resource, API, AUTH_API) {
    var url = API;
    return $resource(
        "",
        {id: "@id", precision: "@precision", geohash:'@geohash'},
        {
            getDrivers: {
                method: 'GET',
                url: url + 'map/driver'
                //http://111.118.241.68:8088/rest/v1/geofence
            },
            getAllDrivers: {
                method: 'GET',
                url: url + 'online/driver',
                isArray:true
            },
            getProfile: {
                method: 'GET',
                url:   url + 'drivers/profile/:id',
                isArray:false
            },
            loadGeoJson: {
                method: 'GET',
                url: AUTH_API + 'geofence'
                //http://111.118.241.68:8088/rest/v1/geofence
            },

            registerList: {
                method: 'GET',
                url: url + 'drivers/registered',
                isArray: true
            },

            inprocessList: {
                method: 'GET',
                url: url + 'drivers/inprocess'
                //isArray:true

            },
            deleteInProcessDriver: {
                method: 'DELETE',
                url: url + 'drivers/:id'
                //isArray:true

            },
            getDemandSupply: {
                method: 'GET',
                url: url + 'supply/demand/indore/auto/:precision',
                isArray: true
            },
            getDemandSupplyHistory: {
                method: 'GET',
                url: url + 'supply/demand/geohash/:geohash',
                isArray: false
            },
            getGroups: {
                method: 'GET',
                url: url + 'surge/group/',
                isArray: true
            },
            heatmap: {
                method: 'GET',
                url: url + 'trips/heatmap',
                isArray: true
            }
        }
    );
}

